import React, {useState} from 'react';
import axios from 'axios';
import './LoginForm.css';
import {API_BASE_URL, ACCESS_TOKEN_NAME} from '../../constants/apiConstants';
import { withRouter } from "react-router-dom";


import Header from '../Header/Header';

import {
    Input,
    Button,
    message
} from 'antd';
import { MailOutlined, LockOutlined } from '@ant-design/icons';

import {
    LoginContainer,
    LoginFormContainer,
    FieldContainer,
    ButtonFieldContainer,
} from './LoginForm.styled';




function LoginForm(props) {
    const [state , setState] = useState({
        email : "",
        password : "",
        successMessage: null
    })

    const handleChange = (value, id) => {
        setState(prevState => ({
            ...prevState,
            [id] : value
        }))
    }

    const handleSubmitClick = (e) => {
        //message.warning('warning test data');
        //message.success('success test data');
        //redirectToHome();
        e.preventDefault();
         const payload={
             "emailID":state.email,
             "password":state.password,
         }
         //http://localhost:8080/login/?email_id=email1&password=1234
        axios.post("/login/", payload)
            .then(function (response) {
                if(response.status === 200){
                    if(response.data.status ==='error')
                    {
                        message.error(response.data.message);

                    }else{
                    localStorage.setItem('orgID',response.data.data.organization.orgID);
                    localStorage.setItem('orgName',response.data.data.organization.orgName);
                    localStorage.setItem('roleName',response.data.data.role.roleName);
                    localStorage.setItem('emailID',response.data.data.user.emailID);
                    localStorage.setItem('userContactNo',response.data.data.user.userContactNo);
                    localStorage.setItem('userID',response.data.data.user.userID);
                    localStorage.setItem('userName',response.data.data.user.userName);
                    // localStorage.setItem('password',payload.password);
                    // localStorage.setItem(ACCESS_TOKEN_NAME,response.data.debugMessage);
                    localStorage.setItem(ACCESS_TOKEN_NAME,response.data.debugMessage);

                    redirectToHome();
                    //props.showError(null)
                    }
                }
                else if(response.code === 204){
                    message.error(response.data.message);
                }
                else{
                    message.error(response.data.message);
                }
            })
            .catch(function (error) {
                console.log(error);
            });
    }
    const redirectToHome = () => {
        props.history.push('/dashboard');
    }
    const redirectToRegister = () => {
        props.history.push('/forgot');
    }
    return(
        <div>
            <Header />
            <LoginContainer>
                <LoginFormContainer>
                    <FieldContainer>
                        <Input
                            prefix={<MailOutlined/>}
                            placeholder="Email ID"
                            type="email"
                            id="email"
                            value={state.email}
                            onChange={(event) => handleChange(event.target.value, 'email')}
                        />
                    </FieldContainer>
                    <FieldContainer>
                        <Input
                            prefix={<LockOutlined/>}
                            type="password"
                            id="password"
                            placeholder="Password"
                            value={state.password}
                            onChange={(event) => handleChange(event.target.value, 'password')}
                        />
                    </FieldContainer>
                    <ButtonFieldContainer>
                        <Button type="primary" onClick={handleSubmitClick}>
                            Log In
                        </Button>
                        <Button type="link" onClick={redirectToRegister} >
                            Forgot Password?
                        </Button>
                    </ButtonFieldContainer>
                </LoginFormContainer>
            </LoginContainer>
        </div>
    )
}

export default withRouter(LoginForm);