    const saveEdit = e => {
        e.preventDefault();

        props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);

                const payload={
                    "userName":values.name,
                    "emailID":values.email,
                    "userContactNo":values.userContactNo,
                    "password":values.password,
                    "userID":localStorage.getItem('userID')
                }

                const reply = axios.patch("/manageUser/profile?user_id="+localStorage.getItem('userID'), payload,{headers: {'Authorization': 'Bearer ' + localStorage.getItem(ACCESS_TOKEN_NAME)}}).then(response => {
                    if(response.status === 200) {
                        if (response.data.status === "error") {
                            message.error(response.data.message);
                        } else {
                            if(response.data.message !== "") {
                                message.success(response.data.message);
                            }

                            // props.history.push('/landing');
                        }
                    } else {
                        message.error("Something went wrong");
                    }
                }).catch(error => {
                    console.log(error.response);
                    if (error.response.status === 401 || error.response.status === 403) {
                        localStorage.clear();
                        message.error("You were logged out!");
                        props.history.push(`/login`);
                    } else {
                        message.error("Unable to contact server");
                    }
                });
            }
        });
    }

